// Create a kind-of media tag for certain browsers.
@mixin browser($browsers...) {
  @each $browser in $browsers {
    html[data-browser*="#{$browser}"] & {
      @content;
    }
  }
}

@mixin section {
  border-bottom: 1px solid $light-grey;
  margin: 20px;
}

@mixin dark-text {
  color: $dark-grey;
}

@mixin medium-text {
  color: $medium-grey;
}

@mixin parallax {
  min-height: 100%;
  background-attachment: fixed;
  background-repeat: no-repeat;

  @media #{$phone}, #{$tablet} {
    background-attachment: scroll
  }
}

@mixin slight-shadow {
  // Generated
  -webkit-box-shadow: 0px 6px 13px 0px rgba(219,219,219,1);
  -moz-box-shadow: 0px 6px 13px 0px rgba(219,219,219,1);
  box-shadow: 0px 6px 13px 0px rgba(219,219,219,1);
}

@mixin slight-inset-shadow {
  // Generated
  -webkit-box-shadow: inset 0px 0px 19px -5px rgba(0,0,0,0.34);
  -moz-box-shadow: inset 0px 0px 19px -5px rgba(0,0,0,0.34);
  box-shadow: inset 0px 0px 19px -5px rgba(0,0,0,0.34);
}
